.container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 20px;
    font-family: Arial, sans-serif;
    background-image: none;
    background-size: cover;
    background-position: center;
    height: 100vh; // 確保背景覆蓋整個視窗高度
    width: 100vw; // 確保背景覆蓋整個視窗寬度
    position: relative; // 讓子元素相對於父元素定位
}

.content {
    max-width: 600px;
    margin-bottom: 20px;
    font-size: 36px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
}

.image {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
}

.button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    position: absolute; // 讓按鈕絕對定位
    top: 50%; // 垂直居中
    left: 50%; // 水平居中
    transform: translate(-50%, -50%); // 使按鈕的中心點在畫面中央
    z-index: 100; // 確保按鈕在最上層

    &:hover {
        background-color: #45a049;
    }
}

.input {
    padding: 10px;
    font-size: 24px;
    width: 100%;
    margin-bottom: 20px;
}

#arjs-video {
    z-index: 50;
}