/* 基本样式设定 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow: hidden;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100vw;
  height: 100vh;
}

html,
body {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
}

/* 通用容器样式 */
.container,
.centered-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 20px;
}

.dialog {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: relative;
}

.centered-image {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.foundButton {
  padding: 15px 30px;
  border-radius: 10px;
  background: #d5f365;
  border: none;
  font-family: inherit;
  color: #2e3730;
  font-weight: bold;
  font-size: 1.5rem;
  cursor: pointer;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.foundButton:hover {
  background-color: #c4e056;
}



body::before {
  content: "";
  position: fixed;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  background-image: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: -1;
  transform: translate(-50%, -50%);
  transform-origin: center;
}

/* 媒体查询 */
@media screen and (max-width: 768px) {
  .foundButton {
    padding: 10px 20px;
    font-size: 1rem;
    bottom: 10px;
  }

  .custom-modal {
    width: 90vw;
    padding: 10px;
  }

  .custom-modal-content {
    font-size: 1.25rem;
  }

  .custom-modal-button {
    padding: 8px 16px;
    font-size: 1rem;
  }

  .assemble-button {
    padding: 8px 16px;
    font-size: 1rem;
  }
}